.form {
  background-color: rgb(68, 71, 73);

  border: 1px solid #000;
  padding: 20px;
  border-radius: 4px;

  margin-bottom: 20px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.label {
  margin-right: 20px;
  color: #fff;
}

.label:last-child {
  margin-bottom: 0px;
}
.inputName {
  margin-left: 10px;
}
.inputNumber {
  margin-left: 10px;
}

.btn {
  color: #31bbbb;
  border: 2px solid #31bbbb;
  background-color: transparent;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  text-align: center;
  text-transform: uppercase;
  border-radius: 10%;
  padding: 5px;
  cursor: pointer;
  transition: all 250ms linear;
}

.btn:hover,
.btn:focus {
  color: orange;
  border: 2px solid orange;
}
